.feed {
  flex: 0.6;
  margin: 0 20px;
}

.feedInputContainer {
  background-color: white;
  padding: 10px;
  padding-bottom: 20px;
  border-radius: 10px;
  margin-bottom: 20px;
}

.feedInput {
  border: 1px solid lightgrey;
  border-radius: 30px;
  display: flex;
  padding: 10px;
  color: gray;
  padding-left: 15px;
}

.feedInput > form {
  display: flex;
  width: 100%;
}

.feedInput > form > input {
  border: none;
  flex: 1;
  margin-left: 10px;
  outline-width: 0;
  font-weight: 600;
}

.feedInput > form > button {
  display: none;
}

.feedInputOptions {
  display: flex;
  justify-content: space-evenly;
}
